import Api from './Api'
import Csrf from './Csrf'


export default {

  async getLinks () {
    await Csrf.getCookie()
    return await Api.get(`link`)
  },

  async createLink (form) {
    await Csrf.getCookie()
    return await Api.post('link/create' , form)
  },

  async updateLink (id, form) {
    await Csrf.getCookie()

    return await Api.put(`updatelink/${id}`, form)
  },

  async getLinkEdit(id){
    await Csrf.getCookie()

    return await Api.get(`link/${id}`)
  },

  // async getLinkReport () {
  //   await Csrf.getCookie()
  //
  //   return await Api.get('link/report')
  // },
  //
  // async getLinkBroke() {
  //   await Csrf.getCookie()
  //
  //   return await Api.get('link/broke')
  // },

  async getWebLink(id) {
    await Csrf.getCookie()

    return await Api.get(`web/link/${id}`)
  },
  async deleteLink(id) {
    await Csrf.getCookie()

    return await Api.delete(`link/${id}`)
  },

  async brokeLink(id) {
    await Csrf.getCookie()

    return await Api.put(`linkbrokereport/${id}`)
  },

  async generate (form) {
    await Csrf.getCookie()

    return await Api.post('web/linkgenerate', form)
  },

  async getGenerate (id, hash) {
    await Csrf.getCookie()

    return await Api.get(`web/linkgenerate/${id}/${hash}`)
  },

  async getLinkGenerate (form, id) {
    await Csrf.getCookie()

    return await Api.put(`web/linkgenerateupdate/${id}`, form)
  },


}
