<template>
  <div class="links-show">
    <section class="section">
      <div class="container">
        <section>

            <b-tabs>
                <b-tab-item :label="`Links ${links.length}`">
                    <b-table
                        :data="links"
                        :paginated="true"
                        :per-page="20"
                        pagination-position="both"
                        >
                        <b-table-column field="id" label="ID" width="30" numeric v-slot="props">
                            {{ props.row.id }}
                        </b-table-column>

                        <b-table-column field="name" label="Name"  v-slot="props">
                            {{ props.row.name }}
                        </b-table-column>

                        <b-table-column field="identy" label="Identy"  v-slot="props">
                            {{ props.row.identy }}
                        </b-table-column>

                        <b-table-column field="actions" label="Actions"  v-slot="props">
                          <b-button
                            type="is-success"
                            label="edit"
                            class="mr-2"
                            tag="router-link"
                            :to="{ name: 'LinkEdit', params: { id: props.row.id }}"></b-button>
                          <b-button
                            class="ml-2"
                            type="is-danger"
                            label="Delete"
                            @click="deleteLink(props.row)"></b-button>
                        </b-table-column>
                    </b-table>
                </b-tab-item>

                <b-tab-item :label="`Links Report ${linksReport.length}`">
                    <b-table
                        :data="linksReport"
                        :paginated="true"
                        :per-page="20"
                        pagination-position="both"
                        >
                        <b-table-column field="id" label="ID" width="30" numeric v-slot="props">
                            {{ props.row.id }}
                        </b-table-column>

                        <b-table-column field="name" label="Name"  v-slot="props">
                            {{ props.row.name }}
                        </b-table-column>

                        <b-table-column field="identy" label="Identy"  v-slot="props">
                            {{ props.row.identy }}
                        </b-table-column>

                        <b-table-column field="actions" label="Actions"  v-slot="props">
                          <b-button
                            type="is-info"
                            label="Try"
                            class="mr-2"
                            tag="a"
                            :href="props.row.url"
                            target="__black"
                            ></b-button>
                          <b-button
                            type="is-primary"
                            label="Broke"
                            class="mr-2 ml-2"
                            @click="brokeLink(props.row)"></b-button>
                          <b-button
                            type="is-success"
                            label="edit"
                            class="mr-2 ml-2"
                            tag="router-link"
                            :to="{ name: 'LinkEdit', params: { id: props.row.id }}"></b-button>
                          <b-button
                            class="ml-2"
                            type="is-danger"
                            label="Delete"
                            @click="deleteLink(props.row)"></b-button>
                        </b-table-column>
                    </b-table>
                </b-tab-item>

                <b-tab-item :label="`Links Broke ${linksBroke.length}`">
                    <b-table
                        :data="linksBroke"
                        :paginated="true"
                        :per-page="20"
                        pagination-position="both"
                        >
                        <b-table-column field="id" label="ID" width="30" numeric v-slot="props">
                            {{ props.row.id }}
                        </b-table-column>

                        <b-table-column field="name" label="Name"  v-slot="props">
                            {{ props.row.name }}
                        </b-table-column>

                        <b-table-column field="identy" label="Identy"  v-slot="props">
                            {{ props.row.identy }}
                        </b-table-column>

                        <b-table-column field="actions" label="Actions"  v-slot="props">
                          <b-button
                            type="is-success"
                            label="edit"
                            class="mr-2"
                            tag="router-link"
                            :to="{ name: 'LinkEdit', params: { id: props.row.id }}"></b-button>
                          <b-button
                            class="ml-2"
                            type="is-danger"
                            label="Delete"
                            @click="deleteLink(props.row)"></b-button>
                        </b-table-column>
                    </b-table>
                </b-tab-item>


            </b-tabs>
        </section>
      </div>
    </section>
  </div>
</template>
<script>
import Link from '@/api/Link'
export default {
  name: 'LinksShow',
  data () {
    return {
      links: [],
      linksReport: [],
      linksBroke: []
    }
  },
  methods: {
    getLinks: function (){
      Link.getLinks().then((response) => {
        this.links = this.selectLink(response.data, 'on')
        this.linksReport = this.selectLink(response.data, 'report')
        this.linksBroke = this.selectLink(response.data, 'off')
      }).catch((errors) => {
        console.log(errors)
      })
    },

    selectLink: function (data, status){
      var array = []
      for(var i = 0; i < data.length; i++){
        if(data[i].status === status){
          array.push(data[i])
        }
      }

      return array
    },

    deleteLink: function (item) {
      this.$buefy.dialog.confirm({
        title: `${item.name}`,
        message: 'Are you sure delete this Link?',
        confirmText: 'Delete Paste',
        type: 'is-danger',
        onConfirm: () => {
          Link.deleteLink(item.id).then((response) => {
            if(response.data.message === 'ok'){
              this.getLinks()
              this.$buefy.notification.open({
                type: 'is-success',
                message: 'Link is Delete!'
              })
            }
          }).catch((errors) => {
            console.log(errors)
          })
        }
      })
    },
    brokeLink: function (item) {
      this.$buefy.dialog.confirm({
        title: `${item.name}`,
        message: 'Are you sure Broke this Link?',
        confirmText: 'Broke Link',
        type: 'is-danger',
        onConfirm: () => {
          Link.brokeLink(item.id).then((response) => {
            if(response.data.message == 'Ok') {
              this.getLinks()
              this.$buefy.notification.open({
                type: 'is-success',
                message: 'Link is Broke!'
              })
            }
          }).catch((errors) => {
            console.log(errors)
          })
        }
      })
    }
  },
  mounted () {
    this.getLinks()
  }
}
</script>
